/* eslint-disable arrow-body-style */
/* eslint-disable react/no-array-index-key */
import React, { useState, useEffect } from 'react';
import './App.module.css';
import axios from 'axios';
import AviaSales from './Aviasales';

const App = () => {
  const [searchId, setSearchId] = useState('');
  const [ticketsList, setTicketsList] = useState([]);
  const [stop, setStop] = useState(false);
  const [list, setList] = useState([]);
  const [firstTickets, setFirstTickets] = useState([]);
  const [mainTickets, setMainTickets] = useState([]);
  console.log(stop);
  console.log(mainTickets);

  useEffect(() => {
    const fetchData = async () => {
      const result = await axios('https://front-test.beta.aviasales.ru/search');
      setSearchId(result.data.searchId);
    };
    fetchData();
  }, []);

  useEffect(() => {
    const fetchData = async () => {
      if (searchId !== '') {
        const result = await axios(
          `https://front-test.beta.aviasales.ru/tickets?searchId=${searchId}`,
        );
        setTicketsList(result.data.tickets);
        setStop(result.data.stop);
      }
    };

    if (searchId && mainTickets.length === 0) {
      if (firstTickets.length === 0) {
        setFirstTickets(ticketsList);
        if (mainTickets.length === 0) {
          fetchData();
        }
      }
      setList(list.concat(ticketsList));
    }
    if (stop === true) {
      return setMainTickets(list);
    }
  }, [searchId, ticketsList, stop, list, firstTickets.length, mainTickets.length]);

  return <AviaSales ticketsList={firstTickets} mainTickets={mainTickets} />;
};

export default App;
